import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import DialogActions from "@mui/material/DialogActions";

const dispatchedEvents = [];

const SrsMuiDialogActions = function (props, ref) {
  return (
    <DialogActions {...props} theme={props.muiTheme}>
      <slot />
    </DialogActions>
  );
};

const styles = `
  /* Custom styles for the MuiDialogActions component */
`;

const props = [
  {
    name: "disableSpacing",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to DialogActions if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiDialogActions),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
